<template>
  <button :class="btnColor">
    <slot></slot>
    <img v-if="cartIcon == 'cart'" src="../assets/images/cart.svg" alt="Cart Icon">
    <img v-else-if="cartIcon == 'plus'" src="../assets/images/plus.svg" alt="Cart Icon">
    <img v-else-if="cartIcon == 'info'" src="../assets/images/info.svg" alt="Cart Icon">
    <img v-else-if="cartIcon == 'finger'" src="../assets/images/finger.svg" alt="Cart Icon">
  </button>
</template>

<script>
export default {
  props: ['btnColor', 'cartIcon']
};
</script>

<style scoped>
.btn {
  border: 0;
  cursor: pointer;
  box-sizing: border-box;
  border-radius: 3px;
  color: #fff;
  display: flex;
  justify-content: space-around;
  position: relative;
}

.btn-small {
  padding: 0.5em 2em;
}

.btn-medium {
  padding: 0.7em 4.5em;
}

.btn-large {
  padding: 0.7em 5em;
}

.btn-info {
  background-color: #2d9cdb;
}

.btn-info:hover,
.btn-info:focus {
  background-color: #58b0e2;
}

.btn-sucess {
  background-color: #27ae60;
}

.btn-sucess:hover,
.btn-sucess:focus {
  background-color: #3fc176;
}

.btn-danger {
  background-color: #e74c3c;
}

.btn img {
  max-width: 20px;
  margin-left: 0.5em;
}
</style>



